body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.bodyContainer>img {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: contain;
  mix-blend-mode: multiply;
  height: 100%;
  /* flex: wrap; */
  align-content: center;
  /* justify-content: space-between;  */
}

.flex-container {
  /* background-color: #dfdfdf; */
  /* margin: 1%; */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  /* align-items: center; */
  height: 98vh;
  width: 98vw;
  /* margin-left: 1%; */
  overflow: hidden;
}

.menu-lateral {
  display: flex;
  flex-direction: row;
  /* border-left: 1px solid #000; */
  /* align-items: flex-start !important; */
  flex: 20%;
  padding: 20px;
}

.bodyContainer {
  flex: 70%;
}



.rootStyle {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #e8e8e8;
  flex-direction: column;
}

.paperStyle {
  padding: 20px;
  width: 80%;
  max-width: 250px;
  margin: auto;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.8) !important;
  /* Agrega un fondo semi-transparente */
  z-index: 2;
}

.loginTitle {
  text-align: center;
  font-size: 1.5rem;
  font-family: 'Lucida Handwriting' !important;
}

h1.loginTitle {
  margin-top: 15vh;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
}

/* input[type="text"]::placeholder { */
input::placeholder {
  font-size: 0.8rem;
  /* Tamaño de la fuente */
  font-family: Arial, sans-serif;
  /* Fuente de la letra */
  color: #999;
  /* Color del texto */
  padding-left: 10px;
  /* Otros estilos CSS que desees aplicar */
}

.textFieldStyle {
  margin-bottom: 10px;
  width: 96%;
  border: 0.5px solid #ccc;
  border-radius: 5px;
  /* font-size: 0.8rem; */
  padding: 5px;
}

.error {
  border: 0.5px solid rgba(92, 41, 41, 0.5) !important;
  outline: none;
  box-shadow: 0 0 5px rgb(221, 50, 27);
}

.textFieldStyle:focus {
  border: 0.5px solid rgb(138, 138, 192) !important;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 0, 255, 0.5);
  /* Agrega un difuminado al borde en foco */
}

.buttonStyle {
  margin-top: 20px;
  width: 100%;
  font-size: 1rem;
}

.imageBack {
  opacity: 0.5 !important;
  z-index: 1 !important;
  position: absolute !important;
  bottom: 0 !important;
  height: 100% !important;
  width: 100% !important;
}

/* Para el S22 ultra portrait*/
@media (width: 1440px) and (height: 3088px) {
  .paperStyle {
    min-height: 30vh;
    max-width: 1000px;
    margin-top: 15vh;
  }

  .loginTitle {
    font-size: 6rem;
  }

  .textFieldStyle {
    margin: 20px 0 60px 0;
    width: 95%;
    font-size: 5rem;
    border-radius: 10px;
  }

  .textFieldStyle:focus {
    border: 0.5px solid rgb(46, 46, 129) !important;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 0, 255, 0.5);
    /* Agrega un difuminado al borde en foco */
  }

  .buttonStyle {
    width: 95%;
    font-size: 4rem !important;
  }

  input::placeholder {
    font-size: 3.5rem;
    /* Tamaño de la fuente */
    font-family: Arial, sans-serif;
    /* Fuente de la letra */
    color: #999;
    /* Color del texto */
    padding-left: 20px;
    /* Otros estilos CSS que desees aplicar */
  }

  .lintoToRedirect {
    font-size: 3rem;
    text-decoration: none;
    margin-top: 20px;
    /* font-family: 'Lucida Handwriting' !important; */
  }

  .swal-modal {
    width: 80%;
    height: 30%;
  }

  .swal-text {
    font-size: 3.8rem;
  }

  .swal-icon {
    width: 200px;
    height: 200px;
    border-width: 15px;
  }

  .swal-icon--error__line {
    width: 170px;
    top: 92px;
  }

  .swal-title {
    font-size: 4rem;
  }

  .swal-button {
    font-size: 3.5rem;
  }
}