.book-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  position: relative;
}

.book {
  width: 100px;
  height: 30vh;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: absolute;
}

/* Estilos para cada libro */
.book:nth-child(1) {
  top: 0;
  left: 0;
  transform: rotate(5deg);
}

.book:nth-child(2) {
  top: 20px;
  left: 30px;
  transform: rotate(-8deg);
}

.book:nth-child(3) {
  top: 10px;
  left: 80px;
  transform: rotate(3deg);
}